<template>
  <a-form-model
    ref="refRuleForm"
    :model="ruleForm"
    :label-col="layout.labelCol"
    :wrapper-col="layout.wrapperCol"
  >
    <a-row>
      <a-col :span="8">
        <a-form-model-item label="" prop="orderNo">
          <a-input v-model="ruleForm.orderNo" placeholder="" />
        </a-form-model-item>
      </a-col>
      <a-col :span="8">
        <a-form-model-item label="" prop="orderStatus">
          <a-select v-model="ruleForm.orderStatus" placeholder="">
            <a-select-option
              v-for="item in OrderStatusDict"
              :key="item.key"
              :value="item.value"
            >
              {{ item.value }}
            </a-select-option>
          </a-select>
        </a-form-model-item>
      </a-col>
      <template v-if="advanced">
        <a-col :span="8">
          <a-form-model-item label="" prop="orderType">
            <a-select v-model="ruleForm.orderType" placeholder="">
              <a-select-option
                v-for="item in OrderTypeDict"
                :key="item.key"
                :value="item.value"
              >
                {{ item.value }}
              </a-select-option>
            </a-select>
          </a-form-model-item>
        </a-col>
        <a-col :span="8">
          <a-form-model-item label="" prop="worker">
            <a-input
              v-model="ruleForm.worker"
              placeholder=""
            />
          </a-form-model-item>
        </a-col>
        <a-col :span="8">
          <a-form-model-item label="" prop="orderPayment">
            <a-select
              v-model="ruleForm.orderPayment"
              placeholder=""
            >
              <a-select-option
                v-for="item in OrderPayMentDict"
                :key="item.key"
                :value="item.value"
              >
                {{ item.value }}
              </a-select-option>
            </a-select>
          </a-form-model-item>
        </a-col>
      </template>

      <a-col :span="(!advanced && 8) || 24">
        <span
          :style="(advanced && { float: 'right', overflow: 'hidden' }) || {}"
        >
          <a-button type="primary" @click="handleSearch">  </a-button>
          <a-button style="margin-left: 8px" @click="handleReset">
            
          </a-button>
          <a @click="toggleAdvanced" style="margin-left: 8px">
            {{ advanced ? "" : "" }}
            <a-icon :type="advanced ? 'up' : 'down'" />
          </a>
        </span>
      </a-col>
    </a-row>
  </a-form-model>
</template>
<script>
import {
  OrderStatusDict,
  OrderTypeDict,
  OrderPayMentDict,
} from "../../configs/dict";

class CreateRuleForm {
  constructor() {
    // 
    this.orderNo = "";
    // 
    this.orderStatus = undefined;
    // 
    this.orderType = undefined;
    // 
    this.worker = "";
    // 
    this.orderPayment = undefined;
  }
}

export default {
  data() {
    return {
      OrderStatusDict,
      OrderTypeDict,
      OrderPayMentDict,

      layout: {
        labelCol: { span: 4 },
        wrapperCol: { span: 17 },
      },
      ruleForm: new CreateRuleForm(),
      advanced: false,
    };
  },
  methods: {
    toggleAdvanced() {
      this.advanced = !this.advanced;
    },
    handleReset() {
      this.ruleForm = new CreateRuleForm();
      this.$emit("reset");
    },
    handleSearch() {
      this.$emit("search", { ...this.ruleForm });
    },
  },
};
</script>
<style lang="less" scoped>
.ant-form-item {
  width: 100%;
}
</style>
